import React, { useEffect, useState } from 'react'
import {
  CWidgetDropdown,
  CRow,
  CCol,
  CDropdown,
  CDropdownMenu,
  CDropdownItem,
  CDropdownToggle,
  CWidgetProgress
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import ChartLineSimple from '../charts/ChartLineSimple'
import ChartBarSimple from '../charts/ChartBarSimple'
import api from '../../api/default';

const WidgetsDropdown = () => {

  const [users, setUsers] = useState([]);
  
  useEffect(() => {
    api.get('/users').then( ({data}) => setUsers(data));
  }, [])
  
  // render
  return (
    <CRow>
      <CCol sm="6" lg="3">
        <CWidgetDropdown
          color="gradient-primary"
          header={users.length.toString()}
          text={`Acampante${users.length == 1 ? '' : 's'} cadastrado${users.length == 1 ? '' : 's'}`}
          footerSlot={
            <ChartLineSimple
              pointed
              className="c-chart-wrapper mt-3 mx-3"
              style={{height: '70px'}}
              dataPoints={[0, 0, 0, 0, 0, 0, users.length]}
              pointHoverBackgroundColor="primary"
              label="Members"
              labels="months"
            />
          }
        >
          <CDropdown>
            <CDropdownToggle color="transparent">
              <CIcon name="cil-settings"/>
            </CDropdownToggle>
            <CDropdownMenu className="pt-0" placement="bottom-end">
              <CDropdownItem>Action</CDropdownItem>
              <CDropdownItem>Another action</CDropdownItem>
              <CDropdownItem>Something else here...</CDropdownItem>
              <CDropdownItem disabled>Disabled action</CDropdownItem>
            </CDropdownMenu>
          </CDropdown>
        </CWidgetDropdown>
      </CCol>

      <CCol sm="6" lg="3">
        <CWidgetDropdown
          color="gradient-info"
          header={users.filter(u => u.isConfirmado).length.toString()}
          text={`Acampante${users.filter(u => u.isConfirmado).length == 1 ? '' : 's'} confirmado${users.filter(u => u.isConfirmado).length == 1 ? '' : 's'}`}
          footerSlot={
            <ChartLineSimple
              pointed
              className="mt-3 mx-3"
              style={{height: '70px'}}
              dataPoints={[0, 0, 0, 0, 0, 0, users.filter(u => u.isConfirmado).length]}
              pointHoverBackgroundColor="info"
              options={{ elements: { line: { tension: 0.00001 }}}}
              label="Members"
              labels="months"
            />
          }
        >
          <CDropdown>
            <CDropdownToggle caret={false} color="transparent">
              <CIcon name="cil-location-pin"/>
            </CDropdownToggle>
            <CDropdownMenu className="pt-0" placement="bottom-end">
              <CDropdownItem>Action</CDropdownItem>
              <CDropdownItem>Another action</CDropdownItem>
              <CDropdownItem>Something else here...</CDropdownItem>
              <CDropdownItem disabled>Disabled action</CDropdownItem>
            </CDropdownMenu>
          </CDropdown>
        </CWidgetDropdown>
      </CCol>
      <CCol sm='6' lg='6'>
        <CWidgetProgress 
          color="success" 
          header={(users.filter(u => u.isConfirmado).length / 35 * 100).toFixed(1).replace(".", ",") + "%"} 
          text="Preenchimento do 1ยบ lote (35)" 
          footer="Valor do lote autal: R$ 150.00"
          value={users.filter(u => u.isConfirmado).length / 35 * 100}
          />
      </CCol>
    </CRow>
  )
}

export default WidgetsDropdown
